const getStatusColor = (status) => {
    switch (status) {
      case 'pending':
        return 'lightblue';
      case 'ongoing':
        return 'rgba(255, 166, 0, 0.333)';
      case 'issues':
        return 'rgba(255, 0, 0, 0.466)';
      case 'completed':
        return 'rgba(0, 128, 0, 0.416)';
      default:
        return 'white';
    }
  };

export default getStatusColor