import React, { useEffect } from 'react';
import SubtaskList from './subTaskList';
import M from 'materialize-css';
import { connect } from 'react-redux';
import { closeCreateTaskModal,  openCreateTaskModal, setParentId, deleteSubtask} from '../../actions'; // Import the action
import EditTaskForm from './EditForm';
import getStatusColor from "../../status"

const Subtask = ({ subtask, onDeleteSubTaskFromList, parentId, openCreateTaskModal, setParentId, onDeleteSubtask}) => {
  useEffect(() => {
    // Initialize Materialize dropdown
    const dropdown = document.querySelectorAll('.dropdown-trigger');
    M.Dropdown.init(dropdown, { constrainWidth: false });

    const collapsible = document.querySelectorAll('.collapsible');
    M.Collapsible.init(collapsible, {});

    const modal = document.querySelectorAll('.modal');
    M.Modal.init(modal, {});
  }, []);

  const handleSetParentId = (subTaskId) => {
    setParentId(subTaskId);
  };

  const handleDeleteSubtask = (subtaskId) => {
    onDeleteSubtask(subtaskId);
  };

  const exportItem = (item) => {
    const projectsToExport = item
    const serializedProjects = JSON.stringify(projectsToExport);

    // Create a Blob from the serialized projects
    const blob = new Blob([serializedProjects], { type: 'application/json' });

    // Create an anchor element to trigger the download
    const a = document.createElement('a');
    const url = URL.createObjectURL(blob);
    a.href = url;

    const dateTimeString = new Date().toISOString().replace(/:/g, '-');

    const filename = `Task_${dateTimeString}.json`;

    a.download = filename;

    // Append the anchor to the document and trigger a click
    document.body.appendChild(a);
    a.click();

    // Remove the anchor and revoke the URL
    document.body.removeChild(a);
    URL.revokeObjectURL(url);

    M.toast({ html: 'Project Export completed!' });
  }

  return (
    <ul className="collapsible">
      <li>
        <div className="collapsible-header" style={{ backgroundColor: getStatusColor(subtask.status) }}>
          <b>{subtask.name}</b>
          <a className='dropdown-trigger right' href='#!' data-target={`dropdown${subtask.id}`}>
            <i className="material-icons">more_vert</i>
          </a>
          <ul id={`dropdown${subtask.id}`} className='dropdown-content'>
            <li><a className="modal-trigger" href="#createTaskModal" onClick={() => handleSetParentId(subtask.id)}>Add SubTask</a></li>
            <li><a className="modal-trigger" href={`#editSubTaskModal${subtask.id}`} onClick={() => {
              handleSetParentId(parentId); 
              sessionStorage.setItem("parentId", parentId);
              sessionStorage.setItem("name", subtask.name);
              sessionStorage.setItem("description", subtask.description);
              }}>Edit Task</a></li>
            <li><a href='#!' onClick={() => handleDeleteSubtask(subtask.id)}>Delete</a></li>
            <li><a href='#!' onClick={() => exportItem(subtask)}>Export</a></li>
            
          </ul>
        </div>
        <div className="collapsible-body">
        <div style={{ maxHeight: '150px', overflowY: 'scroll' }} dangerouslySetInnerHTML={{ __html: subtask.description }} />

          
          {subtask.subtasks && subtask.subtasks.length > 0 && (
            <SubtaskList subtasks={subtask.subtasks} onDeleteSubTask={onDeleteSubTaskFromList} parentId={subtask.id}/>
          )}
        </div>

        {/*Edit task Modal Structure */}
        {/* {task.id} */}
        <div id={`editSubTaskModal${subtask.id}`} className="modal">
          <div className="modal-content">
            {/* Render the CreateTaskForm component here */}
            <EditTaskForm item={subtask} itemId={subtask.id}/>
          </div>
          <div className="modal-footer">
            <a href='#!' className="modal-close waves-effect waves-green btn-flat" onClick={closeCreateTaskModal}>
              Close
            </a>
          </div>
        </div>
      </li>
    </ul>
  );
};

const mapStateToProps = (state) => ({
  // parentId: state.parentId
});

const mapDispatchToProps = {
  setParentId,
  onDeleteSubtask: deleteSubtask
};

export default connect(mapStateToProps, mapDispatchToProps)(Subtask);
// export default Subtask;

