import { connect } from 'react-redux';
import { addProject } from '../../actions';
import { setFormData } from '../../actions/form';
import { useEffect, useRef, useState } from 'react';
import tinymce from 'tinymce/tinymce'; // Import the TinyMCE main module
// import 'tinymce/icons/default'; // Import the default icons
// import 'tinymce/themes/silver'; // Import the theme


const CreateProjectForm = ({ formData, onHandleAddProject, setFormData }) => {
  const editorRef = useRef(null);

  // Initialize TinyMCE on component mount
  useEffect(() => {
    
    if (editorRef.current) {
      console.log("current", editorRef.current)
      tinymce.init({
        target: editorRef.current,
        height: 300,
        menubar: true,
        skin_url: '../../tinymce/skins/ui/oxide',
        icons_url: '../../tinymce/icons/default/icons.min.js',
        theme_url: '../../tinymce/themes/silver/theme.min.js',
        plugins: [
          '../../tinymce/plugins/advlist',
          '../../tinymce/plugins/autolink',
          '../../tinymce/plugins/lists',
          '../../tinymce/plugins/link',
          '../../tinymce/plugins/image',
          '../../tinymce/plugins/preview',
          '../../tinymce/plugins/searchreplace',
          '../../tinymce/plugins/visualblocks',
          '../../tinymce/plugins/code',
          '../../tinymce/plugins/insertdatetime',
          '../../tinymce/plugins/media',
          '../../tinymce/plugins/table',
          '../../tinymce/plugins/wordcount',

          // 'advlist autolink lists link image charmap print preview anchor',
          // 'searchreplace visualblocks code fullscreen',
          // 'insertdatetime media table paste code help wordcount',
        ],
        toolbar: 'undo redo | formatselect | ' +
          'bold italic backcolor | alignleft aligncenter ' +
          'alignright alignjustify | bullist numlist outdent indent | ' +
          'removeformat wordcount | help',
        content_style: 'body { font-family: Helvetica, Arial, sans-serif; font-size: 14px }',
        setup: (editor) => {
          editor.on('input', () => {
            const content = editor.getContent();
            // setFormData({ ...formData, description: content });
            sessionStorage.setItem("description", content)
          });

          editor.on('init', () => {
            console.log("TinyMCE initialized!");
          });
        },
      });
    }

    // Clean up TinyMCE when the component is unmounted
    return () => {
      if (editorRef.current) {
        tinymce.get(editorRef.current.id)?.destroy();
        console.log("remount")
      }
    };
  }, []); // Empty dependency array to run the effect only once on mount

  return (
    <form id='createProjectForm'>
      <label htmlFor="name">Project Name:</label>
      <input
        type="text"
        id="project_name"
        name="project_name"
        value={formData.name}
        onChange={(e) => {setFormData({ ...formData, name: e.target.value }); sessionStorage.setItem("name", e.target.value)}}
        // onChange={(e) => sessionStorage.setItem("name", e.target.value)}
      />

      <label htmlFor="description">Project Description:</label>
      <textarea
        id="project_description"
        name="project_description"
        ref={editorRef}
      />

      <button className='btn' type="button" onClick={() => { onHandleAddProject(formData); setFormData({ name: '', description: '' }); tinymce.get(editorRef.current.id)?.setContent(""); }}>
        Submit
      </button>
    </form>
  );
};

const mapStateToProps = (state) => ({
  formData: state.formData,
});

const mapDispatchToProps = (dispatch) => ({
  onHandleAddProject: (formData) =>{
    const name = sessionStorage.getItem("name")
    const description = sessionStorage.getItem("description")

    const updatedFormData = {
      ...formData,
      name,
      description,
    };

    sessionStorage.setItem("name", "")
    sessionStorage.setItem("description", "")

    dispatch(addProject(updatedFormData))
  } ,
  setFormData: (formData) => dispatch(setFormData(formData)),
});

export default connect(mapStateToProps, mapDispatchToProps)(CreateProjectForm);
