
// // export default App;
import React, { useEffect, useRef } from 'react';
import { connect } from 'react-redux';
import M from 'materialize-css';
import 'materialize-css/dist/css/materialize.min.css';
import Project from './components/project/index';
import CreateProjectForm from './components/project/CreateForm';
import { addProject, deleteProject, setProjects, importRecord, exportRecord } from './actions/index';
import store from './store'
import ProjectCreateFrom from './components/project/CreateForm';


const App = ({ projects, addProject, deleteProject, setProjects, importRecord, exportRecord}) => {
  useEffect(() => {
    const fetchData = async () => {
      try {
        const data = await loadProject();
        console.log('Data fetched:', data);
        
        handleSetProjects(data)

        // Initialize Materialize collapsible
        const coll = document.querySelectorAll('.collapsible');
        M.Collapsible.init(coll, {});

        const modal = document.querySelectorAll('.modal');
        M.Modal.init(modal, {});

        const slide = document.querySelectorAll('.sidenav');
        M.Sidenav.init(slide, {});
      } catch (error) {
        console.error('Error loading projects:', error);
      }

        
    };

    fetchData();
  }, []); // Empty dependency array means this effect runs once after the first render
  // Empty dependency array means this effect runs once after the first render

  const handleAddProject = (newProject) => {
    addProject(newProject);
  };

  const loadProject = async () => {
    // try {
    //   const response = await fetch("/projects.json");
  
    //   if (!response.ok) {
    //     throw new Error(`HTTP error! Status: ${response.status}`);
    //   }
  
    //   const data = await response.json();
    //   console.log(data);
    //   return data;
    // } catch (error) {
    //   console.error('Error fetching the JSON file:', error);
    //   throw error; // Re-throw the error to handle it outside this function if needed
    // }
    if(localStorage.getItem("projects") == null){
      localStorage.setItem("projects", JSON.stringify([]))
      return JSON.parse(localStorage.getItem("projects"))
    }else{
      return JSON.parse(localStorage.getItem("projects"))
    }
    
  };
  

  const handleDeleteProject = (projectId) => {
    deleteProject(projectId);
  };

  const handleSetProjects = (projects) => {
    // console.log("hello")
    setProjects(projects);
  };

  const handleExportRecord = (projects) => {
    const projectsToExport = projects
    const serializedProjects = JSON.stringify(projectsToExport);

    // Create a Blob from the serialized projects
    const blob = new Blob([serializedProjects], { type: 'application/json' });

    // Create an anchor element to trigger the download
    const a = document.createElement('a');
    const url = URL.createObjectURL(blob);
    a.href = url;

    const dateTimeString = new Date().toISOString().replace(/:/g, '-');

    const filename = `projects_${dateTimeString}.json`;

    a.download = filename;

    // Append the anchor to the document and trigger a click
    document.body.appendChild(a);
    a.click();

    // Remove the anchor and revoke the URL
    document.body.removeChild(a);
    URL.revokeObjectURL(url);

    M.toast({ html: 'Project Export completed!' });
  }

  // Assume you have a function to import projects
  const importProjects = (file) => {
    const reader = new FileReader();

    // Read the content of the imported file
    reader.onload = (event) => {
      const importedProjects = JSON.parse(event.target.result);

      // console.log(importedProjects)

      // Assume you have a function to update your state or storage with imported projects
      importRecord(importedProjects);
    };

    // Read the content as text
    reader.readAsText(file);
  };

  return (
    <div className='container' style={{marginBottom: '50px'}}>
      <input
        type="file"
        accept=".json"
        style={{display: 'none'}}
        id='file'
        onChange={(e) => importProjects(e.target.files[0])}
      />
      

      <ul id="slide-out" className="sidenav">
        <li><a href="#!"><i className="material-icons">backup</i>Project Manager</a></li>
        <li><a href="#!" onClick={() => document.getElementById('file').click()}>Import Projects</a></li>
        <li><div className="divider"></div></li>
        <li><a href="#!" onClick={() => handleExportRecord(projects)}>Export Projects</a></li>
      </ul>

      

      <h5 className='center-align'><a style={{float: 'left', marginTop: '15px'}} href="#" data-target="slide-out" className="sidenav-trigger"><i className="material-icons">menu</i></a> Project Manager <a href="#createProjectModal"  className="btn-floating modal-trigger btn-large waves-effect waves-light"><i className="material-icons">add</i></a></h5>
      
      <ul style={{ display: projects.length > 0 ? 'block' : 'none' }} className="collapsible" data-collapsible="accordion">
        {projects.map((project) => (
          <Project key={project.id} project={project} onDeleteProject={handleDeleteProject} />
        ))}
      </ul>

      <h5 style={{ display: projects.length == 0 ? 'block' : 'none' }}>
        No projects at the moment!
      </h5>

      {/* <CreateProjectForm onHandleAddProject={handleAddProject} /> */}
      {/* create project modal trigger */}
      {/* <a className="btn-floating btn-large waves-effect waves-light"><i className="material-icons">add</i></a> */}
      {/* <a className='btn modal-trigger' href="#createProjectModal">Add Project</a> */}

      {/* Modal Structure */}
      <div id="createProjectModal" className="modal">
        <div className="modal-content">
          {/* Render the CreateTaskForm component here */}
          <ProjectCreateFrom/>
        </div>
        <div className="modal-footer">
          <a href='#!' className="modal-close waves-effect waves-green btn-flat">
            Close
          </a>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  projects: state.projects.projects,
});


const mapDispatchToProps = {
  addProject,
  deleteProject,
  setProjects,
  importRecord,
  exportRecord
};

export default connect(mapStateToProps, mapDispatchToProps)(App);

