import React from 'react';
import Subtask from './subTask';

const SubtaskList = ({ subtasks, onDeleteSubTask, parentId }) => (
  <ul className="collapsible">
    {subtasks.map((subtask) => (
      <Subtask key={subtask.id} subtask={subtask} onDeleteSubTaskFromList={onDeleteSubTask} parentId={parentId}/>
    ))}
  </ul>
);

export default SubtaskList;
