// actions/index.js
export const addProject = (newProject) => ({
    type: 'ADD_PROJECT',
    payload: newProject,
});

export const editProject = (newProject) => ({
    type: 'EDIT_PROJECT',
    payload: newProject,
  });
  
  export const deleteProject = (projectId) => ({
    type: 'DELETE_PROJECT',
    payload: projectId,
  });

  export const addTask = (newProject) => ({
    type: 'ADD_PROJECT',
    payload: newProject,
  });
  
  export const addSubtask = (subtaskData) => ({
    type: 'ADD_SUBTASK',
    payload: subtaskData,
  });

  export const editSubtask = (subtaskData) => ({
    type: 'EDIT_SUBTASK',
    payload: subtaskData,
  });
  

  export const openCreateTaskModal = () => ({
    type: 'OPEN_CREATE_TASK_MODAL',
  });
  
  export const closeCreateTaskModal = () => ({
    type: 'CLOSE_CREATE_TASK_MODAL',
  });

  export const setParentId = (parentId) => ({
    type: 'SET_PARENT_ID',
    payload: parentId
  });

  export const deleteSubtask = (payload) => ({
    type: 'DELETE_SUBTASK',
    payload,
  });

  export const setProjects = (payload) => ({
    type: 'SET_PROJECTS',
    payload,
  });

  export const importRecord = (payload) => ({
    type: 'IMPORT_RECORD',
    payload,
  });

  export const exportRecord = (payload) => ({
    type: 'EXPORT_RECORD',
    payload,
  });

  export const exportSubRecord = (payload) => ({
    type: 'EXPORT_SUBRECORD',
    payload,
  });

  