import { connect } from 'react-redux';
import React, { useEffect, useRef, useState } from 'react';
import { editProject } from '../../actions'; // Import your action creator
import { setFormData } from '../../actions/form';
import tinymce from 'tinymce/tinymce'; // Import the TinyMCE main module

const EditProjectForm = ({ formData, onHandleEditProject, setFormData, item, itemId }) => {
  const editorRef = useRef(null);

  
  useEffect(() => {
    // Check if the values have changed before updating
    if (formData[itemId]?.name !== item.name || formData[itemId]?.description !== item.description) {
      setFormData({ ...formData, [itemId]: { name: item.name, description: item.description } });
    }
    console.log(editorRef.current.id)
    if (editorRef.current) {
      tinymce.init({
        target: editorRef.current,
        height: 300,
        menubar: true,
        skin_url: '../../tinymce/skins/ui/oxide',
        icons_url: '../../tinymce/icons/default/icons.min.js',
        theme_url: '../../tinymce/themes/silver/theme.min.js',
        plugins: [
          '../../tinymce/plugins/advlist',
          '../../tinymce/plugins/autolink',
          '../../tinymce/plugins/lists',
          '../../tinymce/plugins/link',
          '../../tinymce/plugins/image',
          '../../tinymce/plugins/preview',
          '../../tinymce/plugins/searchreplace',
          '../../tinymce/plugins/visualblocks',
          '../../tinymce/plugins/code',
          '../../tinymce/plugins/insertdatetime',
          '../../tinymce/plugins/media',
          '../../tinymce/plugins/table',
          '../../tinymce/plugins/wordcount',

          // 'advlist autolink lists link image charmap print preview anchor',
          // 'searchreplace visualblocks code fullscreen',
          // 'insertdatetime media table paste code help wordcount',
        ],
        toolbar: 'undo redo | formatselect | ' +
          'bold italic backcolor | alignleft aligncenter ' +
          'alignright alignjustify | bullist numlist outdent indent | ' +
          'removeformat wordcount | help',
        content_style: 'body { font-family: Helvetica, Arial, sans-serif; font-size: 14px }',
        setup: (editor) => {
          editor.on('input', (e) => {
            const content = editor.getContent();
            // setFormData({ ...formData, description: content });
            console.log(content)
            
            sessionStorage.setItem("description", content)
          });
        },
      });
    }

    // Clean up TinyMCE when the component is unmounted
    return () => {
      if (editorRef.current) {
        tinymce.get(editorRef.current.id)?.destroy();
        // console.log("remount")
      }
    };
  }, [item], formData);

  

  return (
    <form>
      <label htmlFor="name">Project Name:</label>
      <input
        type="text"
        id="name"
        name="name"
        value={formData[itemId]?.name || ''}
        onChange={(e) => {setFormData({ ...formData, [itemId]: { ...formData[itemId], name: e.target.value }}); sessionStorage.setItem("name", e.target.value) }}
        // onChange={(e) => {console.log(e.target.value)}}

      />

      <label htmlFor="description">Project Description:</label>

      <textarea
        id={itemId}
        name="description"
        ref={editorRef}
        value={formData[itemId]?.description || ''}
        // className="materialize-textarea"
        // onChange={(e) => setFormData({ ...formData, [itemId]: { ...formData[itemId], description: e.target.value } })}
        onChange={(e) => {console.log(e.ref.getContent())}}

      />


      <select
        className="browser-default"
        id="status"
        name="status"
        onChange={(e) =>{{
            setFormData({
              ...formData,
              [itemId]: { ...formData[itemId], status: e.target.value },
            });
            sessionStorage.setItem("status", e.target.value)
          }}
        }
      >
        {/* {!formData[itemId]?.status || formData[itemId].status === 'pending' ? (
          <option value="pending">Pending</option>
        ) : null}
        {!formData[itemId]?.status || formData[itemId].status === 'ongoing' ? (
          <option value="ongoing">Ongoing</option>
        ) : null}
        {!formData[itemId]?.status || formData[itemId].status === 'issues' ? (
          <option value="issues">Issues</option>
        ) : null}
        {!formData[itemId]?.status || formData[itemId].status === 'completed' ? (
          <option value="completed">Completed</option>
        ) : null} */}
        <option value="">Select status or leave empty if your are not updating</option>
        <option value="pending">Pending</option>
        <option value="ongoing">Ongoing</option>
        <option value="issues">Issues</option>
        <option value="completed">Completed</option>
      </select>
      <button
        className="btn"
        type="button"
        onClick={() => {
          {
            const data = {
              name: sessionStorage.getItem("name"),
              description: sessionStorage.getItem("description"),
              status: sessionStorage.getItem("status")
            }
            console.log(data)
            // return
            onHandleEditProject(data, itemId);
          }
          // setFormData({ ...formData, [itemId]: { name: '', description: '' } });
        }}
      >
        Submit
      </button>
    </form>
  );
};

const mapStateToProps = (state) => ({
  formData: state.formData,
});

const mapDispatchToProps = (dispatch) => ({
  onHandleEditProject: (formData, projectId) => dispatch(editProject({formData, projectId})),

  setFormData: (formData) => dispatch(setFormData(formData)),
});

export default connect(mapStateToProps, mapDispatchToProps)(EditProjectForm);
