import React, { useEffect } from 'react';
import SubtaskList from '../task/subTaskList';
import M from 'materialize-css';
import CreateTaskForm from '../task/CreateForm';
import EditProjectForm from './EditForm';
import { connect } from 'react-redux';
import { closeCreateTaskModal, setParentId} from '../../actions'; // Import the action 
import getStatusColor from "../../status"

const Project = ({ project, onDeleteProject, onDeleteSubTask, parentId, setParentId, setItem, setItemId }) => {
  useEffect(() => {
    // Initialize Materialize dropdown
    const dropdown = document.querySelectorAll('.dropdown-trigger');
    M.Dropdown.init(dropdown, { constrainWidth: false });
    
    const elems = document.querySelectorAll('.modal');
    M.Modal.init(elems);
  
  }, []);

  const handleSetParentId = (projectId) => {
    setParentId(projectId);
  };

  const exportItem = (item) => {
    const projectsToExport = item
    const serializedProjects = JSON.stringify(projectsToExport);

    // Create a Blob from the serialized projects
    const blob = new Blob([serializedProjects], { type: 'application/json' });

    // Create an anchor element to trigger the download
    const a = document.createElement('a');
    const url = URL.createObjectURL(blob);
    a.href = url;

    const dateTimeString = new Date().toISOString().replace(/:/g, '-');

    const filename = `Project_${dateTimeString}.json`;

    a.download = filename;

    // Append the anchor to the document and trigger a click
    document.body.appendChild(a);
    a.click();

    // Remove the anchor and revoke the URL
    document.body.removeChild(a);
    URL.revokeObjectURL(url);

    M.toast({ html: 'Project Export completed!' });
  }

  return (
    <li style={{marginBottom: '5px'}}>
      <div className="collapsible-header" style={{ backgroundColor: getStatusColor(project.status) }}>
        <b>{project.name}</b>
        <a className='dropdown-trigger right' href='#!' data-target={`dropdown${project.id}`}>
          <i className="material-icons">more_vert</i>
        </a>
        <ul id={`dropdown${project.id}`} className='dropdown-content'>
          <li><a className='modal-trigger' href="#createTaskModal" onClick={() =>{sessionStorage.setItem("parentId", project.id); handleSetParentId(project.id)}}>Add SubTask</a></li>
          <li><a className='modal-trigger' href={`#editProjectModal${project.id}`} onClick={()=> {
            sessionStorage.setItem("name", (project.name)); 
            sessionStorage.setItem("description", (project.description))
            sessionStorage.setItem("status", (project.status))
            }}>Edit Project</a></li>
          {/* <li><a className='modal-trigger' href="#editProjectModal" onClick={() => handleSetItemId(project.id)}>Edit</a></li> */}

          <li><a href='#!' onClick={() => onDeleteProject(project.id)}>Delete</a></li>
          <li><a href='#!' onClick={() => exportItem(project)}>Export</a></li>
          {/* Add other menu options (edit, create subtask) here */}
        </ul>
      </div>
      <div className="collapsible-body">
        <div style={{ maxHeight: '150px', overflowY: 'scroll' }} dangerouslySetInnerHTML={{ __html: project.description }} />

        {project.subtasks && project.subtasks.length > 0 && (
          <SubtaskList subtasks={project.subtasks} onDeleteSubTask={onDeleteSubTask} parentId={project.id}/>
        )}
      </div>
      {/*Create Task Modal Structure */}
      <div id="createTaskModal" className="modal">
        <div className="modal-content">
          {/* Render the CreateTaskForm component here */}
          <CreateTaskForm/>
        </div>
        <div className="modal-footer">
          <a href='#!' className="modal-close waves-effect waves-green btn-flat" onClick={closeCreateTaskModal}>
            Close
          </a>
        </div>
      </div>

      {/*Edit Project Modal Structure */}
      {/* {project.id} */}
      <div id={`editProjectModal${project.id}`} className="modal">
        <div className="modal-content">
          {/* Render the CreateTaskForm component here */}
          <EditProjectForm item={project} itemId={project.id}/>
        </div>
        <div className="modal-footer">
          <a href='#!' className="modal-close waves-effect waves-green btn-flat" onClick={closeCreateTaskModal}>
            Close
          </a>
        </div>
      </div>
    </li>
  );
};

const mapStateToProps = (state) => ({
  parentId: state.parentId,
  item: state.item
});

const mapDispatchToProps = {
  setParentId
};

export default connect(mapStateToProps, mapDispatchToProps)(Project);
// export default Project;
