// projectReducer.js
import { v4 as uuidv4 } from 'uuid';
import M from 'materialize-css';

const initialState = {
        // projects: [
        //     {
        //         id: uuidv4(),
        //         name: 'Project 1',
        //         description: "This is project one",
        //         status: "pending",
        //         subtasks: [
        //             {
        //                 id: uuidv4(),
        //                 name: 'Subtask 1.1',
        //                 description: "This is subtask one of project one",
        //                 status: "ongoing",
        //                 subtasks: [
        //                     {
        //                     id: uuidv4(),
        //                     name: 'Sub-subtask 1.1.1',
        //                     description: "This is subtask one of subtask one of project 1",
        //                     status: "issues",
        //                     },
        //                     // Add more sub-subtasks as needed
        //                 ],
        //             },
        //             // Add more subtasks as needed
        //         ],
        //     },
        // ],

        projects: [],
        formData: {
            name: "",
            description: "",
            status: "pending"
        },

        parentId: "",

        isCreateTaskModalOpen: false
  };

const deleteSubtaskFromItem = (items, subtaskId) => {
return items.map((item) => {
    console.log(item.id === subtaskId)
    if (item.id === subtaskId) {
    // If the item matches the subtaskId, exclude it from the result
    return null;
    } else if (item.subtasks && item.subtasks.length > 0) {
    // Recursively traverse subtasks
    return {
        ...item,
        subtasks: deleteSubtaskFromItem(item.subtasks, subtaskId),
    };
    }
    return item;
}).filter(Boolean); // Remove null values from the result
};
  

const addSubtaskToItem = (items, payload, parentId) => {
    var parentId = parentId;
    var name = payload.formData.name;
    var description = payload.formData.description;
    var status = "pending";
    console.log(status);
    return items.map((item) => {
      if (item.id === parentId) {
        console.log("parentID", parentId);
        return {
          ...item,
          subtasks: [
            ...(item.subtasks || []), // Ensure subtasks is defined
            {
              id: uuidv4(),
              name,
              description,
              status,
              subtasks: [],
              // showHierarchy: false,
              collapsed: true, // Newly generated subtask is initially collapsed
            },
          ],
        };
      } else if (item.subtasks && item.subtasks.length > 0) {
        // Recursively traverse subtasks
        return {
          ...item,
          subtasks: addSubtaskToItem(item.subtasks, payload, parentId),
        };
      }
      return item;
    });
  };

const updateSubtaskInItem = (items, payload, parentId, taskId) => {
    var parentId = parentId;
    var taskId = taskId;
    var name = payload.formData.name;
    var status = payload.formData.status
    var description = payload.formData.description;

    console.log("projects", items)

    return items.map((item) => {
        if (item.id === parentId) {
            const updatedSubtasks = (item.subtasks || []).map((subtask) => {
                if (subtask.id === taskId) {
                    // Update the properties of the subtask
                    return {
                        ...subtask,
                        name,
                        description,
                        status
                    };
                }
                return subtask;
            });

            // Update the project with the updated subtasks
            return {
                ...item,
                subtasks: updatedSubtasks,
            };
        } else if (item.subtasks && item.subtasks.length > 0) {
            // Recursively traverse subtasks
            return {
                ...item,
                subtasks: updateSubtaskInItem(item.subtasks, payload, parentId, taskId),
            };
        }
        return item;
    });
};

  
  
const projectReducer = (state = initialState, action) => {
switch (action.type) {
    case 'ADD_PROJECT':
        const newProject = {
            id: uuidv4(),
            name: action.payload.name,
            description: action.payload.description,
            status: "pending",
            subtasks: [],
        };
        localStorage.setItem("projects", JSON.stringify([...state.projects, newProject]))
        // Enable scrolling on the body
        document.body.style.overflowY = 'auto';
        M.toast({html: 'Project successfully created!'})
        return {
            ...state,
            projects: [...state.projects, newProject],
        };
    case 'EDIT_PROJECT':
        const { name, description, status } = action.payload.formData;
        const projectId = action.payload.projectId;

        console.log(action.payload)
        
        const updateProjects = state.projects.map((project) => {
            if (project.id === projectId) {
                return {
                    ...project,
                    name: name,
                    description: description,
                    status
                };
            }
            return project;
        });
        localStorage.setItem("projects", JSON.stringify(updateProjects))
        document.body.style.overflowY = 'auto';
        M.toast({html: 'Project successfully updated!'})
        return {...state, projects: updateProjects}

    case 'EDIT_SUBTASK':
        const parentId = state.parentId;
        const taskId = action.payload.taskId;
        const projects =  state.projects
        const updatedState = {
            ...state,
            projects: updateSubtaskInItem(projects, action.payload, parentId, taskId),
        };
    
        console.log("state after task update", updatedState);

        localStorage.setItem("projects", JSON.stringify(updatedState.projects))
        document.body.style.overflowY = 'auto';
        M.toast({html: 'TasK successfully updated!'})
        return updatedState;
    
        // return {
        //     ...state,
        //     projects: _updatedProjects,
        // };
        
            
    case 'DELETE_PROJECT':
        const afterDelete =  {
            ...state,
            projects: state.projects.filter((project) => project.id !== action.payload),
        }
        localStorage.setItem("projects", JSON.stringify(afterDelete.projects))
        document.body.style.overflowY = 'auto';
        M.toast({html: 'Project successfully deleted!'})
        return afterDelete;

    case 'ADD_SUBTASK':
        console.log(action.payload)
        // return
        const addSubtaskState = {
            ...state,
            projects: addSubtaskToItem(state.projects, action.payload, state.parentId),
            };
        localStorage.setItem("projects", JSON.stringify(addSubtaskState.projects))
        // Enable scrolling on the body
        document.body.style.overflowY = 'auto';
        M.toast({html: 'Task successfully created!'})
        // console.log(addSubtaskState)
        return addSubtaskState
    case 'SET_PARENT_ID':
        console.log("parant", action.payload)
        const newState = {
            ...state,
            parentId: action.payload
            }

        // console.log("newState", newState)
        return newState

    case 'DELETE_SUBTASK':
        console.log(action.payload);
        const updatedProjects = state.projects.map((project) => {
            return {
            ...project,
            subtasks: deleteSubtaskFromItem(project.subtasks, action.payload),
            };
        });
        localStorage.setItem("projects", JSON.stringify(updatedProjects.projects))
        document.body.style.overflowY = 'auto';
        M.toast({html: 'Task successfully deleted!'})
        // console.log("updatedProjects", updatedProjects);
        return {
            ...state,
            projects: updatedProjects,
        };

    case 'SET_PROJECTS':
        console.log(action.payload)
        return {
            ...state, 
            projects: action.payload
        }

    case 'IMPORT_RECORD':
        console.log(action.payload);
        
        const importedProjects = action.payload;
        
        
        // Assuming state.projects is an array
        const updatedProjectsImport = state.projects.slice(); // Create a copy of the existing projects array
        
        // Loop through the imported projects and update existing ones or add new ones
        try {
            importedProjects.forEach((importedProject) => {
                const existingProjectIndex = updatedProjectsImport.findIndex(
                (project) => project.id === importedProject.id
                );
            
                if (existingProjectIndex !== -1) {
                // Update existing project
                updatedProjectsImport[existingProjectIndex] = importedProject;
                } else {
                // Add new project
                updatedProjectsImport.push(importedProject);
                }
            });
        } catch (error) {
            M.toast({ html: 'Cannot import single project or task!' });
            return {
                ...state,
                projects: state.projects,
            };
        }
        
        
        const updatedStateImport = {
            ...state,
            projects: updatedProjectsImport,
        };

        console.log(updatedStateImport)
        
        localStorage.setItem('projects', JSON.stringify(updatedStateImport.projects));
        document.body.style.overflowY = 'auto';
        M.toast({ html: 'Project Import completed!' });
        
        return updatedStateImport;
        

    case 'EXPORT_RECORD':
        console.log(action.payload)
        return []
            
    default:
        return state;
}
};

export default projectReducer;
  