// formReducer.js/
import { setFormData } from '../actions/form.js';
const initialState = {
    name: '',
    description: '',
  };
  
  const formReducer = (state = initialState, action) => {
    switch (action.type) {
      case 'SET_FORM_DATA':
        return {
          ...state,
          ...action.payload,
        };
      default:
        return state;
    }
  };
  
  export default formReducer;
  